$font-montserrat: 'Montserrat', sans-serif;

.content-wrap+.wp-block-template-part {
    // 구조상 사이드바 겉 박스를 선언할 수밖에
    min-width: 352px;
    width: 30%;
    // background: radial-gradient(circle at center, rgba(123, 107, 188, 0.29) 4%, transparent 70%); 보라색인데 예쁨
    --red: 123;
    --green: 107;
    --blue: 188;
    --opacity: 0.29;
    background: radial-gradient(circle at center, rgba(var(--red), var(--green), var(--blue), var(--opacity)) 4%, transparent 70%);
}

.sidebar-wrap {
    padding: 40px 28px 40px 28px;

    // 포스트리스트쿼리 '에디터픽'
    .wp-block-post-title a {
        color: red;
    }

    .wp-block-image {
        border: red;
    }
}

.section-heading {
    display: inline-block;
    font-family: $font-montserrat;
    font-weight: 400;
    font-size: 20px;
    letter-spacing: -0.5px;
    padding: 0 0 19px 10px;
    color: #3d3d3d;

    em {
        font-weight: 500;
    }
}

.topic-list-item {
    display: inline-block;
    padding: 19px;
    margin: 5px 5px 0 0;
    font-size: 19px;
    border-radius: 50px;
    background: #fcfcfc;
    border: 1px solid #ececec;
    text-decoration: none;
    color: #7a7a7a;

    &:hover {
        border-color: #313131;
        color: #333333;
    }
}